export default {
	data() {
		return {
			modalShow: false,
			luckPosition: 1,
			isWin: false,
			tip: '很遗憾您未中奖，谢谢参与！',
			p1:'客服将会联系您',
			p2:'领取奖品！',
			p:'客服将会联系您领取奖品！'
		}
	},
	mounted() {
		this.$EventBus.$on('closeModal', (onload)=>{
			
			this.luckPosition = onload.luckPosition+1
			this.tip = onload.tip
			// this.p1 = onload.p1
			// this.p2 = onload.p2
			this.isWin = onload.isWin
			this.modalShow = true
		})
	},
	methods:{
		
	},
	beforeDestroy() {
		this.$EventBus.$off('closeModal')
	}
	
}
